import React, { useState, useEffect } from "react";
import AssignmentOutlinedIcon from "@material-ui/icons/AssignmentOutlined";

function App() {
  const [userText, setUserText] = useState("");
  const placeholder = "Type here...";

  useEffect(() => {
    document.title = "Oink Latin";
  }, []);

  function pigLatin(text) {
    return text.replace(/\b(\w{1})(\w*)\b/g, "$2$1ay");
  }

  function handleChange(e) {
    setUserText(e.target.value);
    e.target.style.height = "97px";
    e.target.style.height = e.target.scrollHeight + "px";
  }

  function handleClip() {
    navigator.clipboard.writeText(pigLatin(userText));
  }

  return (
    <div className="appContainer">
      <div class="display">
        <p>{pigLatin(userText)}</p>
        <button onClick={handleClip} className="clipboard">
          <AssignmentOutlinedIcon className="clipIcon" />
        </button>
      </div>

      <textarea
        type="text"
        placeholder={placeholder}
        value={userText}
        onChange={handleChange}
        autoFocus
      />
    </div>
  );
}

export default App;
